export default function localeFilter(val) {
  let key
  if (store.getters.info) {
    key = store.getters.info.locales || 'ru-RU'
  } else {
    key = 'ru-RU'
  }


  return locales[key][val] || `[Locale Error]: Key ${val} not found!`
}
